import React from "react";

import { Row, Column } from "components/Grid";
import { formatDate } from "utils/formatting";

import MarketoForm from "components/product/business-cash-account/MarketoForm";
import videoPaths from "assets/video";
import {
  TitleProps,
  ValuePointsProps,
} from "components/product/business-cash-account/types";

import { HeroProps } from "components/partner-landing-page/brex-cash-pages/types";

import {
  Container,
  Wrapper,
  TitleWrapper,
  ValuePoints,
  DetailsOuter,
  DetailsInner,
  ValuePoint,
  ValuePointText,
  VideoContainer,
  StyledDisabledMobileVideo,
  Footnote,
  ResponsiveIcon,
} from "components/product/business-cash-account/Hero/elements";

const Title = ({ title, orangeText }: TitleProps) => {
  const resultTitle = title.replace(
    orangeText,
    `<span style="color: #FF7348">${orangeText}</span>`,
  );
  return <TitleWrapper dangerouslySetInnerHTML={{ __html: resultTitle }} />;
};

const ValuePointsComponent: React.SFC<ValuePointsProps> = ({ valuePoints }) => (
  <ValuePoints>
    {valuePoints.map(({ iconProps, body }) => (
      <ValuePoint key={body}>
        <ResponsiveIcon {...iconProps} />
        <ValuePointText>{body}</ValuePointText>
      </ValuePoint>
    ))}
  </ValuePoints>
);

const Hero: React.SFC<HeroProps> = ({
  title,
  valuePoints,
  setHeroFormWrapperRef,
  legalYieldDate,
  formId,
  topForm,
  orangeText,
}) => (
  <Container>
    <Row padding={30} gutterWidth={26} verticalAlignment="stretch">
      <Column span={1} spanSmall={0} />
      <Column span={10} spanSmall={12}>
        <Wrapper>
          <Title title={title} orangeText={orangeText} />
          <DetailsOuter>
            <DetailsInner>
              <ValuePointsComponent valuePoints={valuePoints} />
              <MarketoForm
                formId={formId}
                topForm={topForm}
                setFormWrapperRef={setHeroFormWrapperRef}
              />
            </DetailsInner>
            <VideoContainer>
              <StyledDisabledMobileVideo
                preload="auto"
                playsInline
                autoPlay
                loop
                muted
                src={videoPaths.cashPhoneVideo}
              />
              <Footnote>
                <span className="hidden-in-percy">
                  {`* 7-day yield as of ${formatDate(legalYieldDate)}`}
                </span>
              </Footnote>
            </VideoContainer>
          </DetailsOuter>
        </Wrapper>
      </Column>
      <Column span={1} spanSmall={0} />
    </Row>
  </Container>
);

export default Hero;
