import React, { FunctionComponent, useCallback, useRef } from "react";
import styled from "@emotion/styled";

import Layout from "components/layout";
import colors from "styles/colors";

import Hero from "components/partner-landing-page/brex-cash-pages/Hero";
import FloatingButton from "components/product/business-cash-account/FloatingButton";
import Carousel from "components/product/business-cash-account/Carousel";
import GeminiAlternatingDetailList from "components/product/business-cash-account/GeminiAlternatingDetailList";
import QuoteSection from "components/product/business-cash-account/QuoteSection";
import HelpSection from "components/product/business-cash-account/HelpSection";
import BottomSection from "components/product/business-cash-account/BottomSection";
import LegalSection from "components/product/business-cash-account/LegalSection";

import previewImage from "assets/img/brex-cash-preview-image.png";

import {
  detailsProps,
  heroProps,
  quoteProps,
  helpProps,
} from "components/product/business-cash-account/messages";

import { PageProps } from "components/product/business-cash-account/types";

export const Emphasis = styled("span")({
  fontWeight: 500,
});

const BrexCashTemplatePage: FunctionComponent<PageProps> = ({
  location,
  pageContext,
}) => {
  const setBottomFormWrapperRef = useRef<HTMLDivElement>(null);

  const bottomFormWrapperRef = useCallback(() => {
    if (setBottomFormWrapperRef.current) {
      setBottomFormWrapperRef.current.scrollIntoView();
      const inputEl = setBottomFormWrapperRef.current.querySelector("input");

      if (inputEl) {
        inputEl.focus();
      }
    }
    return setBottomFormWrapperRef;
  }, [setBottomFormWrapperRef]);

  const heroPropsData = heroProps({
    marketingYieldRate: pageContext.marketingYieldRate,
  });

  const geminiPropsData = detailsProps({
    marketingYieldRate: pageContext.marketingYieldRate,
    legalYieldDate: pageContext.legalYieldDate,
    legalYieldRate: pageContext.legalYieldRate,
  });

  return (
    <Layout
      location={location}
      title={pageContext.contentfulData.metaTitle}
      description={`Introducing Brex Cash, with an industry-leading ${pageContext.marketingYieldRate}% yield on cash balance, pay zero transaction fees, and seamless integration with your Brex credit card. No minimum balance to get started.`}
      whiteContentBackground
      whiteGutterBackground
      ogImage={previewImage}
      refCode={pageContext.contentfulData.refCode}
      removeToast
      hideDefaultHeader
      hideDefaultFooter
      indexPage={false}
    >
      <Hero
        valuePoints={heroPropsData.valuePoints}
        legalYieldDate={pageContext.legalYieldDate}
        formId={pageContext.contentfulData.marketoFormTopId}
        topForm={pageContext.contentfulData.marketoFormTopPopUpFormId}
        title={pageContext.contentfulData.heroTitle}
        orangeText={pageContext.contentfulData.orangeTextInsideTitle}
      />
      <FloatingButton
        shouldDisplay
        bottomFormWrapperRef={bottomFormWrapperRef}
      />
      <Carousel />
      <GeminiAlternatingDetailList details={geminiPropsData.details} />
      <QuoteSection
        quote={quoteProps.quote}
        speaker={quoteProps.speaker}
        logo={quoteProps.logo}
      />
      <HelpSection
        gutterBackground={colors.offWhite}
        valuePoints={helpProps.valuePoints}
        title={helpProps.title}
        link={helpProps.link}
      />
      <BottomSection
        setBottomFormWrapperRef={setBottomFormWrapperRef}
        formId={pageContext.contentfulData.marketoFormBottomId}
        bottomForm={pageContext.contentfulData.marketoFormBottomPopUpFormId}
      />
      <LegalSection
        gutterBackground={colors.offWhite}
        legalYieldRate={pageContext.legalYieldRate}
        legalYieldDate={pageContext.legalYieldDate}
      />
    </Layout>
  );
};
export default BrexCashTemplatePage;
